.header-block {
  background: #eaf2ff;
  border-radius: 30px;
  height: 30%;
  width: 100%;
  padding-top: 2%;
  padding-left: 5%;
  padding-bottom: 3%;
  position: relative;
}

.heading {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 54px;
  color: #003660;
}

.header-text {
  margin-top: 10px;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: #546e7a;
  width: 50%;
}

.img-doctor {
  width: 23%;
  height: 135%;
  position: absolute;
  bottom: 0px;
  left: 75%;
}

@media only screen and (max-width: 780px) {
  .header-block {
    margin-top: 3%;
  }
}
